  border-radius: 10px;
}

.information-text-element:hover {
  cursor: pointer;
  background-color: rgba(255, 255, 255, 0.075);
}

#information-popup-hour-ranges {
  top: 30%;
}

#choose-goal-ranges-header {
  text-align: center;
  width: 80%;
}

#goal-range-highest-wrapper,
#goal-range-low-wrapper {
  flex-direction: row;
  justify-content: center;
}

.wrapper-in-menus-text {
  font-size: 1.2rem;
  font-weight: 500;
  color: var(--header-white);
  width: 232px;
  text-align: center;
}

.color-picker {
  height: 47px;
  width: 45px;
  padding: 5px;
  background-color: transparent;
}

.color-picker:hover {
  cursor: pointer;
  background-color: rgba(255, 255, 255, 0.075);
}

.color-picker::-webkit-color-swatch {
  border: none;
  border-radius: 50%; /* Makes the color swatch circular */
}

#save-track-project-button,
#save-goals-per-week-button {
  padding-block: 10px;
  width: 120px;
}

/* project tracking in settings */

#track-project-settings-wrapper {
  margin-top: 30px;
}

#delete-image {
  height: 40px;
}

#current-project-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

/* goal / tracking project in app text */

.info-text-in-app-wrapper {
  width: var(--width-window-mainscreen);
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  margin-top: 20px;
  border-radius: 10px;
  color: var(--header-white);
  padding: 10px;
  background-color: rgba(84, 95, 125, 0.233);
  border: 1px solid rgba(255, 255, 255, 0.055);
  box-shadow: var(--box-shadow);
}

#current-project-tracking-in-app-wrapper {
  align-items: start;
}

#tracking-project-text-in-app {
  background-color: rgba(15, 114, 109, 0.452);
  color: rgba(29, 219, 207, 0.959);
  box-shadow: var(--box-shadow);
  padding: 5px;
  border-radius: 10px;
  font-size: 1.15rem;
  font-weight: 400;
  align-items: start;
}

#hours-tracked-project {
  font-size: 1rem;
  margin-left: 5px;
}

#goal-in-app-text {
  font-size: 1rem;
  background-color: rgba(15, 114, 109, 0.452);
  color: rgba(29, 219, 207, 0.959);
  padding: 9px;
  border-radius: 10px;
  box-shadow: var(--box-shadow);
}

#goal-evaluation-text {
  font-size: 1.15rem;
  margin-left: 5px;
}

#goal-evaluation-text-sum-hours {
  font-size: 0.9rem;
  margin-left: 5px;
}

#goal-in-app-wrapper {
  align-items: start !important;
}

.in-app-text {
  color: var(--header-white);
  font-size: 1.2rem;
  font-weight: 400;
}

#delete-goal-button {
  position: absolute;
  top: 5%;
  right: 2%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 30px;
  width: 32px;
  border-radius: 100%;
  color: rgb(145, 64, 64);
  font-size: 1.5rem;
  background-color: rgba(255, 255, 255, 0);
  font-weight: 400;
}

#delete-goal-button:hover {
  cursor: pointer;
  background-color: rgba(133, 54, 54, 0.699);
}

/* Statistics Menu */

#statistics-header {
  margin-top: 30px;
}

#statistics-btn {
  /* for possible use later */
  display: none;
}

#yearly-recap-wrapper {
  background-color: rgb(27, 52, 82);
  padding-bottom: 100px;
}

#calendars-and-yearly-review-wrapper {
  position: relative;
  display: grid;
  grid-template-columns: minmax(0, 0.6fr) minmax(0, 0.4fr);
  grid-template-rows: auto 1fr;
  gap: 50px;
  width: 95%;
  min-height: 0;
  padding-bottom: 30px;
}

#calendars-wrapper-yearly-recap {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  margin-top: 50px;
  margin-left: 20px;
  justify-content: space-around;
  padding-bottom: 30px;
  border-radius: 16px;
  grid-row: 1/3;
  grid-column: 1/2;
}

#yearly-text-and-review-wrapper {
  width: 90%;
  line-height: 2;
  padding-inline: 20px;
  margin-top: 50px;
  margin-bottom: 50px;
  margin-inline: auto;
  max-width: 700px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  grid-column: 2/3;
  grid-row: 1/2;
  align-self: start;
  align-items: start;
}

#yearly-text-and-review-wrapper h2 {
  margin-left: 10px;
}

#textarea-yearly-review {
  height: 400px;
  width: 90%;
  overflow: auto;
  max-width: 700px;
  grid-column: 2/3;
  grid-row: 2/3;
  padding: 1rem;
  resize: none;
  background-color: rgba(255, 255, 255, 0.925);
  border: 1px solid rgba(255, 255, 255, 0.1);
  line-height: 1.6;
  border-radius: 16px;
  box-shadow: var(--box-shadow);
  font-size: 1.2rem;
  color: rgba(24, 24, 24, 0.747);
  transition:
    box-shadow 0.3s ease,
    border-color 0.3s ease;
}

#textarea-yearly-review:focus {
  border-color: rgba(255, 255, 255, 0.068);
  box-shadow: 0 0 0 2px rgba(29, 29, 29, 0.2);
  outline: none;
}

label[for='textarea-yearly-review'] {
  color: white;
  font-size: 1.3rem;
}

#yearly-statistics-header,
#yearly-statistics {
  margin-bottom: 10px;
  font-weight: 400;
  text-align: start;
}

#yearly-recap-project-and-hours-text {
  margin-inline: auto;
}

.seperation-line {
  width: 97%;
  height: 1px;
  margin-inline: auto;
  background-color: rgba(150, 150, 150, 0.473);
}

#years-select {
  font-size: 1.6rem;
  background-color: rgba(255, 255, 255, 0.096);
  color: white;
  border-radius: 16px;
  padding: 10px;
  box-shadow: var(--box-shadow);
}

#years-select:hover {
  cursor: pointer;
  background-color: rgba(255, 255, 255, 0.171);
}

option {
  color: black;
}

#save-yearly-review-button {
  width: 20%;
}

#project-wrapper-statistics {
  margin-inline: auto;
  width: 96%;
  padding-inline: 10px;
  display: flex;
  justify-content: space-around;
  background-color: rgba(87, 69, 95, 0.349);
  border-radius: 16px;
  box-shadow: var(--box-shadow);
}

/* Progress bar */

#progress-bar-and-checkmark-wrapper,
#progress-bar-and-checkmark-wrapper-week-statistics {
  position: relative;
  display: flex;
  align-items: center;
  width: 92%;
}

#progress-bar-and-checkmark-wrapper-week-statistics {
  margin-left: 13px;
}

#checkmark,
#checkmark-weeks-statistics {
  position: absolute;
  right: 1%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 30px;
  width: 30px;
  color: white;
  background-color: rgb(87, 137, 160);
  box-shadow: var(--box-shadow);
  border-radius: 100%;
  transition-duration: 200ms;
}

#checkmark-weeks-statistics {
  right: 0;
  height: 50px;
  width: 50px;
}

#progress-bar-wrapper,
#progress-bar-and-checkmark-wrapper-week-statistics {
  width: 93%;
  height: 12px;
  background-color: rgba(168, 213, 219, 0.103);
  border-radius: 16px;
  box-shadow: var(--box-shadow);
}

#progress-bar-and-checkmark-wrapper-week-statistics {
  width: 95%;
}

#progress-bar,
#progress-bar-weeks-statistics {
  background-color: rgb(41, 144, 157);
  width: 100%;
  height: 100%;
  border-radius: 16px;
  transition-duration: 200ms;
}

@media screen and (max-width: 1183px) {
  #calendars-wrapper-yearly-recap {
    display: none;
  }

  #calendars-and-yearly-review-wrapper {
    display: flex;
    flex-direction: column;
  }

  #textarea-yearly-review-form {
    width: 100%;
  }

  #calendars-and-yearly-review-wrapper {
    background-color: rgba(255, 255, 255, 0);
    box-shadow: none;
  }
